@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@600&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #1B1C1D; */
  box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

code {
  font-family: Roboto, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mb-0 {
  margin-bottom: 0;
}


.awssld{--organic-arrow-thickness: 6px;--organic-arrow-border-radius: 10px;--organic-arrow-height: 30px;--control-button-width: 10%;--control-button-height: 25%;--control-button-background: transparent;--slider-transition-duration: 400ms;--control-bullet-color: #2d5182;--control-bullet-active-color: #26456f;--content-background-color: #fff;border:1px solid red;margin-bottom:2rem;border:1px solid darkgrey;padding:0.5rem}@media (max-width: 425px){.awssld{--slider-height-percentage: 180%}.awssld .column{font-size:3vw}.awssld .column .image{width:100%;max-height:300px}}.awssld #project--self{border:10px solid red}.awssld__bullets{bottom:-30px}.awssld__bullets button{height:12px;width:12px}

